.Navbar {
    display: flex;
    width: 100%;
    height: auto;
    background: #c82127;

    margin: -5px -5px 20px -5px;
    padding: 0 15px 0 15px;

    border: solid;
    border-width: 5px;
    border-color: #fed766;
    box-shadow: 1px 1px 1px #6868684d;

    font-family: "Poppins";
    font-weight: 800;
    font-size: 30px;

    /* position: fixed; */
    /* z-index: 2; */
}

.Navbar ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;

    margin-left: -35px;
    padding-right: 50px;

    width: 100%; 
    height: auto;
}

.Navbar ul li {
    list-style-type: none;
    color: #ffffff;

    cursor: pointer;

    padding: 0 10px 0 10px;
}

.homeLink {
    display: flex;
}

.Navbar a {
    text-decoration: none;
    color: #ffffff;
    width: auto
}

.Navbar a:hover {
    text-decoration: none;
    color: #fed766;
    transition: 0.15s;
}

.side {
    margin: auto 25px auto auto;
}

@media only screen and (max-width: 600px) {
    .Navbar {
        font-size: 23px;
        padding-left: 10px;
    }

    .logo-container {
        max-width: 40px;
        max-height: 40px;
    }

    .Navbar ul li {
        padding: 5px;
    }
    
    .Navbar .side {
        display: none;
    }
}