.Discord {
    font-size: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-family: 'Poppins', sans-serif;

    padding: 80px 0 30px 0;

    background: #ffffff;

    text-align: left;

    color: #000;
    height: fit-content;
    width: 100vw;
}

.icon {
    color: #5460e6;

    padding: 0 35px 35px 35px;

    width: auto;
    height: auto;
}

.container {
    padding: 0px 15px 15px 25px;
}

.description {
    font-size: 25px;    
    width: 50vw;
}

.Discord .image {
    background-image: url('../img/discord.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 15%;
    width: 200px;
    height: 200px;
    transition: 0.2s;
    opacity: 1; 
}

.Discord .image:hover {
    background-image: url('https://cdn.discordapp.com/icons/703048054198239305/ba1e6c01bcab493d66dc2a291c31bd92.png?size=128');
    border-radius: 50%;
    width: 200px;
    transition: 0.2s;
    opacity: 1; 
}

.icon:hover {
    color: #848efa;
    transition: 0.15s;
}

@media only screen and (max-width: 600px) {
    .Discord {
        padding: 30px 0 20px 20px;
    }

    .Discord .title {
        line-height: 55px;
    }

    .Discord .icon {
        display: flex;
        justify-content: center;
        margin-left: -35px;
    }

    .Discord .description {
        font-size: 6vw;
        width: 80vw;
        padding-top: 10px;
    }
}