.About {
    font-size: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-family: 'Poppins', sans-serif;

    padding: 80px 0 80px 0;

    background: #ffffff;

    text-align: left;

    color: #000;
    height: fit-content;
    width: 100vw;
}

.About .title {
    width: 100%;
}

.About .container {
    display: flex;
    flex-wrap: wrap;
    width: 68vw;
    justify-content: left;
}

.About .description {
    text-align: left;
    font-size: 25px;    
    width: 50vw;
}

@media only screen and (max-width: 600px) {
    .About {
        padding: 30px 0 30px 0;
    }

    .About .description {
        font-size: 6vw;
        width: 100vw;
    }
}