.Footer {
    font-size: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-family: 'Poppins', sans-serif;

    padding: 80px 0 80px 0;

    background: #fff;

    text-align: left;

    color: #000;
    height: fit-content;
    width: 100vw;
}

.button {
    background: #fed766;
    color: #000;
    border-radius: 10px;
    text-decoration: none;
    padding: 20px;
    transition: 0.15s;
}

.button:hover {
    /* background: #009FB7; */
    background: #e4252b;
    color: #fff;
    transition: 0.15s;
    cursor: pointer
}
