.Contact {
    font-family: 'Poppins', sans-serif;
    color: #000;

    font-size: 40px;
    font-weight: 200;

    text-align: left;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    margin: 125px auto 225px auto;

    width: 50vw;
}

.Contact .container {
    flex-basis: 100%;
    display: flex;
    justify-content: center;
    padding: 0px 35px 15px 35px;
}

.Contact input {
    padding: 10px;

    font-size: 20px;
    font-family: 'Poppins';

    width: 90%;
    
    border: none;
    outline: none;
    border-radius: 5px;
}

.Contact textarea {
    padding: 10px;
    
    font-size: 20px;
    font-family: 'Poppins';
    
    width: 90%;
    max-width: 90%;
    min-width: 90%;

    height: 150px;

    border: none;
    outline: none;
    border-radius: 5px;
}

.submit {
    background: #fed766;
    color: #000;

    text-decoration: none;
    border-radius: 10px;
    padding: 20px;
    transition: 0.15s;
}

.submit:hover {
    background: #e4252b;
    color: #fff;
    text-decoration: none;
    transition: 0.15s;
    cursor: pointer
}

@media only screen and (max-width: 600px) {
    .Contact {
        width: 100vw;
        line-height: 55px;
        font-size: 35px;
        margin-top: 45px;
    }
}