.Testimonials {
    font-size: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-family: 'Poppins', sans-serif;

    margin: 80px 0 80px 0;

    text-align: left;

    color: #000;
    height: fit-content;
    width: 100vw;
}

.tweet {
    min-width: 400px;
    padding: 15px;
}

.tweet-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 25px;
    width: 100%;
}

@media only screen and (max-width: 600px) {
    .Testimonials {
        padding: 0 0 30px 0;
        margin: 40px 0 0 0;
        text-align: center;
        line-height: 50px;
        width: 100vw;
    }

    .tweet {
        min-width: 300px;
    }
}