.logo-container {
    width: 50px;
    height: auto;
    stroke: #fff;
    stroke-width: 8px;
    opacity: 95%;
}

.logo-container .cls-1 {
    fill:#c82127;
}
.logo-container .cls-1, .cls-2, .cls-3 {
    fill-rule: evenodd;
}
.logo-container .cls-2 {
    fill: #ededee;
}
.logo-container .cls-3 {
    fill: #fff;
}