.Socials {
    font-size: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    font-family: 'Poppins', sans-serif;

    padding: 80px 0 30px 0;

    text-align: left;

    color: #000;
    height: fit-content;
    width: 100vw;
}

.icon {
    color: #5460e6;

    padding: 0 35px 35px 35px;

    width: auto;
    height: auto;
}

.title {
    width: 100%;
    font-size: 40px;
}

.container {
    padding: 0px 15px 15px 25px;
}

.description {
    font-size: 25px;    
    width: 50vw;
}

.Socials .image {
    background-image: url('../img/twitter.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 15%;
    width: 200px;
    height: 200px;
    transition: 0.2s;
    opacity: 1; 
}

.Socials .image:hover {
    background-image: url('https://pbs.twimg.com/profile_images/1305999693403557888/Ej5H4jpS_200x200.jpg');
    border-radius: 50%;
    width: 200px;
    transition: 0.2s;
    opacity: 1; 
}

.icon:hover {
    color: #848efa;
    transition: 0.15s;
}

@media only screen and (max-width: 600px) {
    .Socials {
        padding: 30px 0 20px 20px;
    }

    .Socials .title {
        line-height: 55px;
    }

    .Socials .icon {
        display: flex;
        justify-content: center;
        margin-left: -35px;
    }

    .Socials .description {
        font-size: 6vw;
        width: 80vw;
        padding-top: 10px;
    }
}